@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&family=Tangerine:wght@700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  
}
:root{
  --bg:  #0c2169;
  /* --bg2: #f1d0f7; */
  --main: #141b40;
  --white: #f0f0f0;
  --black: #000;
}

body{
  background-color: var(--bg);
 
}

.header{
 margin: 10px;
 position: absolute;
 top: 0;

}
.header .mid{
 width: 100%;
 height: 100vh;
 position: fixed;
 top: 0;
 left: 0;
 background-color: rgba(12,33,100,0.7);
}
.header .imgs{
  margin-top: 8vh;
  
}
.header .imgs img{
  width: 60%;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
.header .selt{
  margin: 10px;
  padding: 10px;
}
.header .selt input.select{
  width: 100%;
  outline: none;
  border-radius: 50px;
  border: none;
  padding: 15px 20px;
}

.opm{
 width: 200%;
 height: 100vh;
 background-size: cover;
position: fixed;
}

.start {
  position: fixed;
  width: 100%;
  bottom: 0;
  margin-bottom: 60px;
}
.start button{
  background-color: var(--main);
  color: var(--white);
  border: none;
  font-size: 19px;
  border-radius: 50px;
  font-weight: 900;
  outline: none;
  padding: 20px 12px;
  display: flex;
  justify-content: center;
  width: 70%;
  margin: 0 auto;
}
.start .dedicate{
  display: flex;
  justify-content: center;
  margin-top: 10px;
  color: white;
}
.question{
  width: 90%;
  margin: 3.5em auto;

}
.question .q1{
  color: var(--white);
  background-color: var(--main);
  height: 150px;
  display: flex;
  border-radius: 10px;
  padding: 8px;
  font-size: 22px;
  font-family: sans-serif;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
}
.question .q1.try{
  height: 50px;
  position: relative;
  top: 60px;
  border-radius: 10px;
}
.question .q1.gt{
  background-color: unset;
  margin-top: -75px;
}
.question .q1 .q_num{
  background-color: var(--main);
  width: 80px;
  border-radius: 50px;
  height: 50px;
  font-size: 20px;
  margin-top: -80px;
  align-items: center;
  display: flex;
  justify-content: center;
}
.question .q1 .q2{
  margin: 5px;
  position: relative;
  top: 20px;
}
.question .q1 .q2.wn{
  top: 0;
  font-size: 150%;
  font-weight: 700;
}
.question .ans{
  display: flex;
  flex-direction: column;
  margin-top: 20px;

  
}
.question .ans button{
  display: flex;
  border: none;
  outline: none;
  background-color: var(--main);
  border-radius: 10px;
  width: 100%;
  align-items: center;
  /* justify-content: space-between; */
  color: var(--white);
  font-size: 18px;
  margin: 7px auto;
  padding: 10px 12px;
}
.question .ans button .opt{
  background-color: var(--white);
  color: var(--black);
  font-weight: 600;
  width: 40px;
  display: flex;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  height: 40px;
}
.question .ans button p.opt_ans{
  margin-left: 15px;
}
.question .keys{
  position: fixed;
  width: 90%;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}
.question .keys button{
  width: 60px;
  height: 60px;
  padding: 10px;
  border: none;
  outline: none;
  background-color: var(--main);
  color: var(--white);
  border-radius: 10px;
} 

.question .keys button p i{
  font-size: 20px;
}

.question .ans p.score{
  text-align: center;
  font-size: 20px;
  color: white;
}